
.black{
    color: black;
}
.white{
    color: white;
}
li{
    list-style-type: none;
}
.wrapper{
    width: 80%;
    height: 100%;
}
.wrapper__center{
    display: flex;
    align-items: center;
    justify-content: center;
}
.wrapper__flex{
    display: flex;
}
@media screen and (max-width: 1000px){
    .wrapper{
        width: 90%;
    }
}
.title-first{
    font-size: 2.8125rem;
    font-weight: 700;
}
.title-second{
    font-size: 1.75rem;
    font-weight: lighter;
    padding: 20px 0;
}
.desc{
    font-size: 1.125rem;
    padding: 15px 0;
}

.button{
    width: fit-content;
    background-color: grey;
    padding: 15px;
    color: white;
    font-weight: 700;
    font-size: 1.25rem;
    cursor: pointer;
}
.button:hover{
    background: rgb(0, 0, 0)000;
}

/* SCROLL */

/* полоса прокрутки (скроллбар) */
::-webkit-scrollbar {
    width: 16px; /* ширина для вертикального скролла */
    height: 8px; /* высота для горизонтального скролла */
    background-color: white;
}

/* ползунок скроллбара */
::-webkit-scrollbar-thumb {
    background-color: grey;
    border-radius: 5em;
}

::-webkit-scrollbar-thumb:hover {
    background-color: black;
}
