h3{
    font-size: 1.5rem;
    text-align: left;
}
.welcome-section {
    height: 100vh;
    display: flex;
    align-items: flex-end;
    display: flex;
    flex-direction: column;
}
.why-us {
    height: 100vh;
    display: flex;
    justify-content: center;
    /* padding: 5rem 0; */
    background: url('../assets/svg/blade.svg') no-repeat left bottom;
    background-size: 80%;
}
.welcome-section__conteiner {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}
.light{
    font-weight: lighter;
}
.first-page,
.second-page {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.first-page {
    flex: 3;
    background: url('../assets/svg/blueprint.svg') no-repeat right;
    background-size: 80%;

}

.second-page {
    flex: 1;
    border-left: 10px grey solid;
    background: url('../assets/svg/blade.svg') no-repeat right 4%, #F5F5F5;
}

.why-us__conteiner{

    padding: 20px;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: start;
}
.why-us__position{
    display: flex;
    justify-content: space-between;
}
.list{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: flex-start;
    flex-wrap: wrap;
}
.item{
    /* width: 50%; */
    font-size: 0.8rem;
    margin: 2rem;
    font-weight: 700;
    text-align: left;
}
.why-us__bg{
    background: #F5F5F5;
    height: auto;
}
.why-us__item{
    font-size: 1rem;
    margin: 1.5rem;
    width: 100%;
}

.flex-left{
    display: flex;
    flex-direction: column;
    align-items: end;
}

.licenses__conteiner{
    width: 95%;
    display: flex;
    flex-wrap: wrap;
}
.licenses__link{
    width: 40%;
}
.licenses__img{
    width: 100%;
}
.list__image{
    display: flex;
}
.proj__image{
    max-width: 20rem;
    height: 18rem;
    margin: 0 5px;
}
@media screen and (max-width: 800px) {
    .proj__image{
        max-width: 15rem;
        height: 13.5rem;
        margin: 0 2px;
    }
}
.item__text{
    font-size: 1.4rem;
}

/* CONTACTS */
.contacts{
    height: 100vh;
    display: flex;
    flex-direction: column;
}
.contacts__wrapper{
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
}
.contacts__conteiner{
    display: flex;
}
.contacts__items{
    padding: 10px 0;
    font-size: 1.75rem;
}
.map{
    width: 55rem;
    height: 38rem;
}


@media screen and (max-width: 1500px) {
    .contacts__conteiner{
        flex-wrap: wrap;
    }
}