header{
    padding: 10px 0;
    border-bottom: #f5f5f5 1px solid;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
}
.tel{
    text-decoration: none;
    font-size: 2rem;
    color: black;
}
.logo{
    width: 180px;
    height: 140px;
    cursor: pointer;
}

@media screen and (max-width: 800px) {
    .logo{
        width: 80px;
        height: 60px;
    }
}


.header__conteiner{
    display: flex;
    justify-content: space-between;
    align-items: center;
}